@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

.menuBtn {
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.menuBtn div,
.menuBtn div:before,
.menuBtn div:after {
  background: #1a1a1a;
  border-radius: 10px;
  width: 40px;
  height: 5px;
  transition: all 0.15s linear;
}

.menuBtn div:before,
.menuBtn div:after {
  content: "";
  position: absolute;
}

.menuBtn div:before {
  transform: translateY(-200%);
}

.menuBtn div:after {
  transform: translateY(200%);
}

.menuBtn.open div {
  background: transparent;
}

.menuBtn.open div:before {
  transform: rotate(45deg);
}

.menuBtn.open div:after {
  transform: rotate(-45deg);
}

.title {
  text-align: center;
  font-size: 45px;
  margin: 25px 0px;
  color: #1a1a1a;
}

.item {
  background: #f6f6f8;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 0px 20px 2px #ccc;
  max-width: 300px;
}

.item .item_header a {
  display: block;
  max-height: 210px;
  overflow: hidden;
}

.item .item_header a img {
  display: block;
  width: 100%;
  min-height: 210px;
  object-fit: cover;
  transition: all 0.2s;
}

.item .item_footer {
  padding: 7px;
}

.item .item_footer h3 {
  margin-top: 10px;
  margin-bottom: 10px;
}

.item .item_footer h3 a {
  color: #1a1a1a;
}

.item .item_footer p {
  color: #5d5d5d;
  font-size: 14px;
}

.item .item_footer .add {
  display: block;
  text-align: center;
  border-radius: 16px;
  background: #f55d22;
  color: #fff;
  padding: 7px 14px;
  font-size: 14px;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
}

.item .item_footer .add span {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 500px;
  left: 0;
  width: 100%;
  height: 100%;
  background: #dd4b11;
  transition: top 0.2s;
}

.item .item_footer .add:hover span {
  top: 0;
}

.item:hover .item_header a img {
  transform: scale(1.2);
}

.item:hover .item_footer h3 a {
  color: #f55d22;
}

.top_nav {
  background: #f6f6f8;
}

.top_nav .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 50px;
  width: 100%;
}

.top_nav .top_nav__block {
  display: flex;
}

.top_nav .top_nav__block a {
  display: block;
  text-decoration: none;
  margin-right: 15px;
}

.top_nav .top_nav__block a svg {
  width: 30px;
  height: 30px;
  fill: #f55d22;
}

.top_nav .top_nav__block a:last-child {
  margin-right: 0;
}

.top_nav .top_nav__block a {
  font-weight: bold;
}

nav {
  position: sticky;
  background: #fff;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 99999;
  box-shadow: 0 6px 36px rgba(0, 0, 0, 0.09);
}

nav .container {
  display: flex;
  justify-content: space-between;
  min-height: 80px;
  align-items: center;
  position: relative;
}

nav .container .nav_menu .menuBtn {
  display: none;
}

nav .container .nav_menu ul {
  display: flex;
  justify-content: space-between;
}

nav .container .nav_menu ul li {
  list-style-type: none;
}

nav .container .nav_menu ul li a {
  display: flex;
  height: 80px;
  align-items: center;
  padding: 8px;
  color: #1a1a1a;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
  z-index: 5;
  transition: color 0.4s;
  letter-spacing: 1px;
}

nav .container .nav_menu ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0%;
  top: 0;
  left: 0;
  background: #f55d22;
  transition: height 0.3s;
  z-index: -1;
}

nav .container .nav_menu ul li a:hover {
  color: #fff;
}

nav .container .nav_menu ul li a:hover::after {
  height: 100%;
}

