header {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
  background-attachment: fixed;
  position: relative;
  height: 90vh;
  overflow: hidden;
}

header::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(48, 13, 1, 0.6862745098);
}

header .header_content {
  display: flex;
  flex-flow: column;
  align-content: center;
  align-items: center;
  position: relative;
  color: #fff;
  z-index: 55;
  text-align: center;
  width: 100%;
}

header .header_content h1 {
  text-transform: uppercase;
  font-size: 55px;
  letter-spacing: 2px;
}

header .header_content p {
  line-height: 30px;
  margin-bottom: 20px;
  max-width: 600px;
}

header .header_content .header_content__buttons {
  display: flex;
}

header .header_content .header_content__buttons a,
header .header_content .header_content__buttons button {
  display: block;
  padding: 15px 40px;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
}

header .header_content .header_content__buttons a {
  color: #fff;
  background: #f55d22;
  margin-right: 20px;
  transition: background 0.3s;
}

header .header_content .header_content__buttons a:hover {
  background: #dd4b11;
}

header .header_content .header_content__buttons button {
  color: #fff;
  border: 2px solid #fff;
  background: transparent;
  font-size: 16px;
  text-transform: uppercase;
}

header .header_content .header_content__buttons button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background: rgba(255, 255, 255, 0.316);
  transition: width 0.2s;
  z-index: -1;
}

header .header_content .header_content__buttons button:hover::before {
  width: 100%;
  z-index: -1;
}

header .scrollDown {
  width: 100%;
  position: relative;
  text-align: center;
  z-index: 55;
  cursor: pointer;
  top: 180px;
  animation: chgitem 0.6s linear alternate infinite;
}

header .scrollDown svg {
  width: 70px;
  height: 70px;
  fill: #fff;
}

@keyframes chgitem {
  from {
    top: 170px;
  }

  to {
    top: 210px;
  }
}

main {
  height: 3500px;
}

main .items_wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-gap: 20px;
}

main .items_wrapper .item {
  max-width: 100%;
}

