body {
  font-family: NSARegular, "Roboto", sans-serif;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  overflow: hidden;
}

body::-webkit-scrollbar-track {
  background: #ccc;
}

body::-webkit-scrollbar-thumb {
  background: #f55d22;
}

p {
  line-height: 24px;
}

a {
  color: #4088FC;
  text-decoration: none;
}

a:hover {
  color: #f55d22;
}

