@media screen and (max-width: 768px) {
  .title {
    font-size: 30px;
  }

  nav .container .nav_menu .menuBtn {
    display: flex;
  }

  nav .container .nav_menu ul {
    flex-flow: column;
    justify-content: flex-start;
    position: absolute;
    top: 0px;
    width: 580px;
    overflow: hidden;
    left: -5000px;
    background: #f6f6f8;
    transition: left 0.2s linear;
  }

  nav .container .nav_menu ul li a {
    height: 60px;
    padding: 30px 20px;
  }

  nav .container .nav_menu ul.active_nav {
    left: 0px;
    height: 100vh;
  }

  .title {
    font-size: 30px;
  }

  header {
    height: 600px;
  }

  header .scrollDown {
    top: 60px !important;
    animation-play-state: paused;
  }

  header .header_content h1 {
    font-size: 44px;
  }

  header .header_content p {
    font-size: 16px;
  }

  .title {
    font-size: 30px;
  }
}

